.titleBar {
  background-color: #000;
  display: flex;
  color: #fff;
  width: 100%;
  justify-content: space-between;
  padding-left: 10px
}

.TBody{ width: 75%; 
 
  background: rgba(139, 62, 11, 0.856);
  margin: 15px auto;
  border-radius: 6px;
  display: flex;
  justify-items: center;
  flex-direction: row;

}

.img-td{
  margin-right:15px;
  float: left;
  
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

p{
  text-align: justify;
  padding-left: 15px;
  padding-right: 25px;
}

h3{
  padding-left: 15px;
}

.button{
  margin-left: 15px;
}

.button:hover {cursor: pointer;}

img{
  border-radius: 10px;
}

.searchme{
  width: 18% !important;
  height: 10px;
  margin-top: 25px;
  margin-right: 16px;
  border-radius: 25px;
  font-Size: calc(5px + 1vw);
  cursor: auto;
  
}

.searchme:focus {outline:0;}
.searchme:hover {border:2px solid rgb(106, 177, 218);}
